/* Material Modal */
.modalFixedBg {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: #000;
	background-color: rgba(0, 0, 0, .6);
	z-index: 12;
	display: flex;
	justify-content: center;
	align-items: center;
}
.modalContainer {
	min-width: 400px;
	max-width: 90%;
	min-height: 200px;
	max-height: 90vh;
	background: #fff;
	border-radius: 4px;
	margin: 0 auto;
	overflow-x: hidden;
}
.modalClose {
	position: absolute;
	color: #fff;
	right: 15px;
	font-size: 20px;
}

/* Material Input */
.materialInput {
	width: 100%;
	height: 30px;
	border-bottom: 2px solid #2874f0;
	/* margin-bottom: 10px; */
}
.materialInput input {
	width: 100%;
	flex: 1;
	outline: none;
	border: none;
	z-index: 1;
	background: transparent;
	line-height: 30px;
}
.input::placeholder{
	font-size: 12px;
}

/* Material Button */
.materialButton {
	width: 100%;
	display: inline-block;
	padding: 10px;
	font-size: 15px;
	outline: none;
	background-color: #fb641b;
	color: #ffffff;
	box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .2);
	border: none;
	cursor: pointer;
	border-radius: 2px;
}
.materialButton:hover {
	box-shadow: 0 4px 6px 0 rgba(0, 0, 0, .12);
}

